// Module
___CSS_LOADER_EXPORT___.push([module.id, `#discuss {
  font-family: 'Lato', 'PingFang SC', 'Microsoft YaHei', sans-serif;
}
#discuss .discuss-header {
  padding: 20px;
  color: #555;
  font-size: 16px;
}
#discuss .discuss-header .discuss-count {
  color: #6190e8;
  border-bottom: 1px dotted #6190e8;
  margin-right: 5px;
}
#discuss .discuss-header .hr {
  margin: 10px 0 0 0;
}
#discuss .discuss-header .discuss-user {
  display: flex;
  justify-content: end;
  cursor: pointer;
}
#discuss .controls {
  float: right;
}
#discuss .controls .controls-tip-icon {
  color: #6190e8;
  margin-right: 4px;
}
#discuss .controls .controls-tip {
  margin-right: 20px;
  color: #6190e8;
}
#discuss .reply-form {
  overflow: hidden;
}
#discuss .reply-form .reply-form-controls {
  float: right;
  padding: 10px 0;
}
#discuss .reply-form .reply-form-controls .tip {
  color: #c2c2c2;
  margin-right: 8px;
  font-size: 13px;
}
#discuss .ant-comment-content:hover .icon-delete {
  display: inline-block;
}
#discuss .ant-popover-open .icon-delete {
  display: inline-block;
}
#discuss .icon-delete.active {
  display: inline-block;
}
#discuss .ant-comment-inner {
  padding: 6px 0;
}
#discuss .ant-comment-actions {
  margin-top: 0;
}
#discuss .ant-comment-content-detail p {
  white-space: inherit;
  margin-bottom: 0;
}
@media screen and (max-width: 736px) {
  .controls {
    width: 100%;
    margin-top: -10px;
  }
  .controls .disscus-btn {
    width: 100%;
  }
  .controls .controls-tip,
  .controls .controls-tip-icon {
    display: none;
  }
}
