___CSS_LOADER_EXPORT___.push([module.id, `.app-article {
  min-height: 100vh;
  height: calc(100% - 45px);
  margin-top: 32px;
  padding: 0 0 72px 0;
}
.app-article .post-header {
  text-align: center;
  padding: 20px 0;
  border-bottom: 1px solid #e8e8e8;
}
.app-article .post-header .post-title {
  color: #0d1a26;
  font-size: 1.7em;
  font-weight: 400;
  margin-top: 0;
}
.app-article .post-header .others {
  vertical-align: middle;
  margin-top: 10px;
  color: rgba(0, 0, 0, 0.45);
}
.app-article .post-header .others a {
  color: inherit;
}
.app-article .post-content {
  display: flex;
  flex-direction: row;
}
.app-article .post-content .article-userInfo {
  width: 20vw;
  border-right: 1px solid #fff;
}
.app-article .post-content .article-detail {
  flex: 1;
  padding: 20px;
  font-size: 17px;
  font-weight: 800;
  color: black;
  font-family: "Microsoft YaHei", "SimSun", "STHeiti";
}
